"use client";

import { useEffect, useState, useRef, useCallback } from "react";
import { Calendar, MapPin, Clock, Users, User } from "lucide-react";

interface Event {
    id: string;
    title: string;
    description: string;
    date: string;
    time: string;
    location: string;
    eventType: string;
    maxAttendees?: number;
    privacy: string;
    createdBy: string;
    createdAt: string;
}

// Mock data - replace with actual API call
const mockEvents: Event[] = Array.from({ length: 50 }, (_, i) => ({
    id: `event-${i + 1}`,
    title: `Event ${i + 1}: ${['Coffee Meetup', 'Game Night', 'Hiking Trip', 'Movie Screening', 'Book Club', 'Yoga Session', 'Food Festival', 'Concert'][i % 8]}`,
    description: `Join us for an amazing ${['Coffee Meetup', 'Game Night', 'Hiking Trip', 'Movie Screening', 'Book Club', 'Yoga Session', 'Food Festival', 'Concert'][i % 8]}! This is a great opportunity to meet new people and have fun together.`,
    date: new Date(Date.now() + i * 24 * 60 * 60 * 1000).toISOString().split('T')[0],
    time: `${10 + (i % 12)}:${(i % 2 === 0 ? '00' : '30')}`,
    location: ['Central Park', 'Downtown Cafe', 'Mountain Trail', 'Cinema Complex', 'Library', 'Wellness Center', 'City Square', 'Concert Hall'][i % 8],
    eventType: ['social', 'sports', 'food', 'entertainment', 'outdoor', 'online', 'other'][i % 7],
    maxAttendees: i % 3 === 0 ? undefined : 10 + (i % 20),
    privacy: i % 2 === 0 ? 'public' : 'invite-only',
    createdBy: `User ${(i % 5) + 1}`,
    createdAt: new Date(Date.now() - i * 60 * 60 * 1000).toISOString(),
}));

export default function Feed() {
    const [events, setEvents] = useState<Event[]>([]);
    const [loading, setLoading] = useState(false);
    const [hasMore, setHasMore] = useState(true);
    const [page, setPage] = useState(0);
    const observerRef = useRef<IntersectionObserver | null>(null);
    const loadingRef = useRef<HTMLDivElement>(null);

    const itemsPerPage = 10;

    const loadEvents = useCallback(async () => {
        if (loading || !hasMore) return;
        
        setLoading(true);
        
        // Simulate API call delay
        await new Promise(resolve => setTimeout(resolve, 800));
        
        const startIndex = page * itemsPerPage;
        const endIndex = startIndex + itemsPerPage;
        const newEvents = mockEvents.slice(startIndex, endIndex);
        
        if (newEvents.length === 0) {
            setHasMore(false);
            setLoading(false);
            return;
        }
        
        setEvents(prev => [...prev, ...newEvents]);
        setPage(prev => prev + 1);
        setLoading(false);
    }, [page, loading, hasMore]);

    useEffect(() => {
        loadEvents();
    }, []);

    useEffect(() => {
        observerRef.current = new IntersectionObserver(
            (entries) => {
                if (entries[0].isIntersecting && hasMore && !loading) {
                    loadEvents();
                }
            },
            { threshold: 0.1 }
        );

        const currentRef = loadingRef.current;
        if (currentRef) {
            observerRef.current.observe(currentRef);
        }

        return () => {
            if (currentRef && observerRef.current) {
                observerRef.current.unobserve(currentRef);
            }
        };
    }, [loadEvents, hasMore, loading]);

    const formatDate = (dateString: string) => {
        const date = new Date(dateString);
        const today = new Date();
        const tomorrow = new Date(today);
        tomorrow.setDate(tomorrow.getDate() + 1);
        
        if (date.toDateString() === today.toDateString()) {
            return 'Today';
        } else if (date.toDateString() === tomorrow.toDateString()) {
            return 'Tomorrow';
        } else {
            return date.toLocaleDateString('en-US', { 
                weekday: 'short', 
                month: 'short', 
                day: 'numeric' 
            });
        }
    };

    const formatTime = (timeString: string) => {
        const [hours, minutes] = timeString.split(':');
        const hour = parseInt(hours);
        const ampm = hour >= 12 ? 'PM' : 'AM';
        const displayHour = hour % 12 || 12;
        return `${displayHour}:${minutes} ${ampm}`;
    };

    return (
        <div className="min-h-screen bg-gradient-to-b from-amber-50 via-white to-amber-50/30">
            <main className="max-w-4xl mx-auto px-6 py-8 md:py-12">
                <div className="mb-8">
                    <h1 className="text-4xl md:text-5xl font-bold mb-3 text-gray-900 tracking-tight">
                        Event Feed
                    </h1>
                    <p className="text-lg text-gray-600">
                        Discover and join the latest events happening near you
                    </p>
                </div>

                <div className="space-y-6">
                    {events.map((event) => (
                        <div
                            key={event.id}
                            className="bg-white rounded-xl border border-amber-900/20 shadow-md hover:shadow-xl transition-all duration-300 overflow-hidden"
                        >
                            <div className="p-6 md:p-8">
                                {/* Header */}
                                <div className="flex items-start justify-between mb-4">
                                    <div className="flex-1">
                                        <h2 className="text-2xl font-bold text-gray-900 mb-2">
                                            {event.title}
                                        </h2>
                                        <div className="flex items-center gap-2 text-sm text-gray-500">
                                            <User className="w-4 h-4" />
                                            <span>{event.createdBy}</span>
                                            {event.privacy === 'invite-only' && (
                                                <span className="px-2 py-0.5 bg-amber-100 text-amber-900 rounded text-xs font-medium">
                                                    Invite Only
                                                </span>
                                            )}
                                        </div>
                                    </div>
                                    <span className="px-3 py-1 bg-amber-100 text-amber-900 rounded-lg text-xs font-semibold capitalize">
                                        {event.eventType}
                                    </span>
                                </div>

                                {/* Description */}
                                <p className="text-gray-700 mb-6 leading-relaxed">
                                    {event.description}
                                </p>

                                {/* Event Details */}
                                <div className="grid md:grid-cols-2 gap-4 mb-6">
                                    <div className="flex items-center gap-3 text-gray-700">
                                        <div className="p-2 bg-amber-100 rounded-lg">
                                            <Calendar className="w-5 h-5 text-amber-900" />
                                        </div>
                                        <div>
                                            <div className="text-sm text-gray-500">Date</div>
                                            <div className="font-semibold">{formatDate(event.date)}</div>
                                        </div>
                                    </div>

                                    <div className="flex items-center gap-3 text-gray-700">
                                        <div className="p-2 bg-amber-100 rounded-lg">
                                            <Clock className="w-5 h-5 text-amber-900" />
                                        </div>
                                        <div>
                                            <div className="text-sm text-gray-500">Time</div>
                                            <div className="font-semibold">{formatTime(event.time)}</div>
                                        </div>
                                    </div>

                                    <div className="flex items-center gap-3 text-gray-700">
                                        <div className="p-2 bg-amber-100 rounded-lg">
                                            <MapPin className="w-5 h-5 text-amber-900" />
                                        </div>
                                        <div>
                                            <div className="text-sm text-gray-500">Location</div>
                                            <div className="font-semibold">{event.location}</div>
                                        </div>
                                    </div>

                                    {event.maxAttendees && (
                                        <div className="flex items-center gap-3 text-gray-700">
                                            <div className="p-2 bg-amber-100 rounded-lg">
                                                <Users className="w-5 h-5 text-amber-900" />
                                            </div>
                                            <div>
                                                <div className="text-sm text-gray-500">Max Attendees</div>
                                                <div className="font-semibold">{event.maxAttendees}</div>
                                            </div>
                                        </div>
                                    )}
                                </div>

                                {/* Actions */}
                                <div className="flex gap-3 pt-4 border-t border-gray-100">
                                    <button className="flex-1 px-6 py-2.5 bg-amber-900 text-white font-semibold rounded-lg hover:bg-amber-800 transition-all duration-200 shadow-md hover:shadow-lg">
                                        Join Event
                                    </button>
                                    {event.privacy === 'public' && (
                                        <button className="px-6 py-2.5 bg-white text-gray-700 font-semibold rounded-lg border-2 border-amber-900/30 hover:border-amber-900 hover:bg-amber-50 transition-all duration-200">
                                            Request to Join
                                        </button>
                                    )}
                                </div>
                            </div>
                        </div>
                    ))}
                </div>

                {/* Loading indicator */}
                <div ref={loadingRef} className="py-8">
                    {loading && (
                        <div className="flex items-center justify-center">
                            <div className="animate-spin rounded-full h-10 w-10 border-b-2 border-amber-900"></div>
                            <span className="ml-4 text-gray-600">Loading more events...</span>
                        </div>
                    )}
                    {!hasMore && events.length > 0 && (
                        <div className="text-center text-gray-500 py-8">
                            <p>No more events to load</p>
                        </div>
                    )}
                </div>
            </main>
        </div>
    );
}

